const setLimitationError = (limitation) => {
  // eslint-disable-next-line no-console
  console.error(`  (data-limitation="${limitation}") -  .    .`);
};

export const getLimitationsRegEx = (limitation) => {
  switch (limitation) {
    case 'digit':
      return /[^\d]/g;
    case 'name':
      return /[^a-zA-Z--\-\s]/g;
    case 'letters':
      return /[^a-zA-Z--\s]/g;
    case 'letters-and-digit':
      return /[^a-zA-Z--\s\d]/g;
    case 'cyrillic':
      return /[^--\s]/g;
    case 'latin':
      return /[^a-zA-Z\s]/g;
    default:
      return setLimitationError(limitation);
  }
};

export const getMatrixLimitationsRegEx = (matrix) => {
  switch (matrix) {
    case 'digit':
      return /[^\d]/g;
    case 'name':
      return /[^\-\-\a-z\A-Z\-]]/g;
    case 'letters':
      return /[^\-\-\a-z\A-Z]/g;
    case 'letters-and-digit':
      return /[^\-\-\a-z\A-Z\d]/g;
    case 'cyrillic':
      return /[^\-\-]/g;
    case 'latin':
      return /[^\a-z\A-Z]/g;
    default:
      return false;
  }
};

export const getMailRegEx = () => /[a-zA-Z--0-9]{1}([a-zA-Z--0-9\-_\.]{1,})?@[a-zA-Z--0-9\-]{1}([a-zA-Z--0-9.\-]{1,})?[a-zA-Z--0-9\-]{1}\.[a-zA-Z--]{2,6}/;
